.tasks {
    list-style: none;
    margin: 0;
    padding: 0;
    
}


.tasks__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
    grid-gap: 10px;
    padding: 10px;
    border-bottom: 1px solid #cfcfcf;
    }

    

.tasks__content--done {
    text-decoration: line-through;   
}
    
.tasks__button {
    min-width: 30px;
    min-height: 30px;
    padding: 5px;
    border: none;
    cursor: pointer;
    transition: 0.5s;
    
}

.tasks__button.tasks__button--done {
    background-color: hsl(120, 100%, 25%);
    color: #ffffff;
}

.tasks__button.tasks__button--done:hover {
    background-color: hsl(120, 100%, 35%);
}

.tasks__button.tasks__button--remove {
    background-color: #ff0000;
    color: #ffffff;
}

.tasks__button.tasks__button--remove:hover {
    background-color: hsl(0, 100%, 65%); 
}

.tasks--hidden {
    display: none;
}